@import "index.scss";

#top-bar {
  width: 100%;
  height: 3rem;
  background-color: $blue-dark;
  color: $white-high;
  display: flex;
  justify-content: right;
  align-items: center;

  h2 {
    margin: 1rem;
    background-color: $blue-light;
    color: white;
    border-radius: 0.5rem;
    padding: 0.25rem 0.5rem;
  }

  #logout {
    height: 2rem;
    width: 5rem;
    margin: 0.5rem;
    border-radius: 0.5rem;
    border: none;
    background-color: white;
    color: red;
    display: flex;
    align-items: center;
    justify-content: space-around;

    img {
      height: 1rem;
    }
  }
}
