@import "index.scss";

#side-bar {
  height: calc(100% - 3rem);
  width: 20rem;
  color: $white-mid;
  margin: 0;
  padding: 0;
  background-color: $blue-light;
  display: flex;
  flex-direction: column;
  align-items: center;

  #nav-container {
    width: 100%;
    height: 1px;
    flex-grow: 1;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    padding: 1rem;

    a {
      background-color: $white-high;
      width: calc(100% - 2rem);
      height: 2rem;
      margin: 0.5rem;
      box-sizing: border-box;
      padding-left: 1rem;
      border-radius: 1rem;
      display: flex;
      align-items: center;
      text-decoration: none;
      color: $black-high;
    }
  }

  #buttons {
    width: 90%;
    height: 5rem;
    background-color: $white-high;
    border-radius: 5rem;
    margin: 1rem 0;
  }
}
